function convert(str) {
  var positions = '';

  for (var i = 0; i < str.length; i++) {
    if (str[i].match(/[A-Z]/) !== null) {
      positions += " ";
      positions += str[i].toLowerCase();
    } else {
      positions += str[i];
    }
  }
  return positions;
}

